/* import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');  */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Roboto', sans-serif;
}
:root{
  --primary:#212121 ;
}
.fas {
  font-size: 1.2em;
}
.navbar {
  background-color: var(--primary);
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  align-items: center;
}
.navbar  {
  height: 60px;
  width: 100%;
  grid-column: 2 / 3;
}
.navbar .menu {
  grid-column: 4 / 7;
  display: flex;
}
.navbar ul li {
  margin-right: 3em;
  list-style: none;
}
.navbar ul li {
  text-decoration: none;
  color: white;
  cursor: pointer;
}
.navbar ul li a:hover {
  color: black;
  
}
.navbar .menu-bar {
  display: none;
}
.navbar .shop {
  grid-column: 8 / 9;
  justify-content: flex-end;
}
.hero {
  height: 83vh;
   background-image: linear-gradient(rgba(16, 16, 16, .4), rgba(16, 16, 16, .5)) , url(./assets/IMG.jpg);
  background-size: cover;
  background-position: center;
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(8, 1fr);
  position: relative;
}
.hero .info-content {
  grid-column: 7 / 10;
  grid-row: 3 / 7;
  color: #fff;
  z-index: 1;
}
.hero .info-content h3 {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.8em;
}
.hero .info-content h1 {
  font-weight: 900;
  font-size: 5em;
  color: var(--primary);
}
.hero .info-content p {
  font-family: 'Montserrat', sans-serif;
}
.hero .info-content .btn {
  font-size: 1em;
  color:#fff;
  background: none;
  padding: 10px 20px;
  border-radius: 30px;
  border:1px solid var(--primary);
  margin-top: 10px;
}
.hero .info-content .btn:hover {
  background-color: var(--primary) ;
  color:#181818;
}
.about{
  position: absolute;
  margin-top:50px;
  margin-bottom: 50px;
  width: 100%;
}
.flex-container{
  display: flex;
  text-align: center;
}
.flex-container section{
  
  text-align: center;

}

.about h2 {
  text-align: center;
  align-items: center;
  font-size: 2em;
  font-weight: bold;
  margin-bottom: .3em;
  color:gray;
}

.about .divider, .products .divider {
  background-color: gray;
  width: 50px;
  height: 3px;
  margin: 0 auto;
}

.about .about-content {
  width: 90%;
  margin: 0 auto;
  display: grid;
  display: flex;
  align-items: center;
}

.about .about-info {
  margin: 2em;
  background-color: rgb(255 255 255 / 70%);
  border-radius: 32px;
  padding: 15px;

}

.about .about-info h3 {
  text-transform: uppercase;
  margin-bottom: 1em;
}

.footer {
  background-color: DimGray;
  color: #fff;
  padding: 30px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

